<template>
  <div style="">
    <transition-group name='fade' tag='div' :style="dimension" class="relative center" >
      <div v-for="i in [currentIndex]" :key='i' class="center" >
        <img class="" :src="currentImg" :style="dimension" />
      </div>
    </transition-group>

    <div class="center">
      <a class="prev" @click="prev" href='javascript:void(0)'>&#10094;</a>
      <input v-for="(item, index) in images" :key="index" type="radio" :id="'slide' + index" @click="selected(index)" :checked="currentIndex===index">
      <a class="next" @click="next" href='javascript:void(0)'>&#10095;</a>
    </div>
  </div>
</template>

<script>
// https://github.com/alligatorio/vue-image-slider
// https://www.digitalocean.com/community/tutorials/vuejs-create-image-slider

export default {
  name: 'SliderSecond',
  props: {
    dimension: {
      type: String,
      default: "height:300px;max-width:100%",
    },
  },
  data() {
    return {
      images: [
        'https://cdn.pixabay.com/photo/2015/12/12/15/24/amsterdam-1089646_1280.jpg',
        'https://cdn.pixabay.com/photo/2016/02/17/23/03/usa-1206240_1280.jpg',
        'https://cdn.pixabay.com/photo/2015/05/15/14/27/eiffel-tower-768501_1280.jpg',
        'https://cdn.pixabay.com/photo/2016/12/04/19/30/berlin-cathedral-1882397_1280.jpg'
      ],
      timer: null,
      currentIndex: 0,
      //dimension: "height:300px;max-width:100%",
    }
  },
  mounted: function() {
    this.startSlide();
  },
  methods: {
    startSlide: function() {
      this.timer = setInterval(this.next, 4000);
    },
    next: function() {
      // this.currentIndex += 1
      this.currentIndex = (this.currentIndex + 1) % this.images.length;
      this.resetTimer();
    },
    prev: function() {
      // this.currentIndex -= 1
      this.currentIndex = (this.currentIndex + this.images.length - 1) % this.images.length;
      this.resetTimer();
    },
    selected: function(index) {
      this.currentIndex = index;
      this.resetTimer();
    },
    resetTimer: function() {
      clearInterval(this.timer);
      this.timer = setInterval(this.next, 4000);
    },
  },
  computed: {
    currentImg: function() {
      return this.images[Math.abs(this.currentIndex) % this.images.length];
    }
  }
  
}
</script>

<style>
.fade-enter-active,
.fade-leave-active {
  transition: all 0.9s ease;
  /*transition: opacity .3s;*/
  overflow: hidden;
  visibility: visible;
  position: absolute;
  width:100%;
  opacity: 1;
}

.fade-enter,
.fade-leave-to {
  visibility: hidden;
  width:100%;
  opacity: 0;
}
/*
img {
  height:600px;
  width:100%
}
*/

.prev, .next {
  cursor: pointer;
  /*
  position: absolute;
  top: 40%;
  */
  width: auto;
  /*padding: 16px;*/
  padding: 5px;
  /*color: white;*/
  color: blue;
  font-weight: bold;
  font-size: 18px;
  transition: 0.7s ease;
  /*border-radius: 0 4px 4px 0;*/
  /*border-radius: 50%;*/
  border-radius: 4px 4px 4px 4px;
  text-decoration: none !important;
  user-select: none;
}

/* Position the "next button" to the right */
/*
.next {
  right: 0;
}

.prev {
  left: 0;
}
*/
/* On hover, add a black background color with a little bit see-through */
.prev:hover, .next:hover {
  /*background-color: rgba(0,0,0,0.9);*/
  background-color: rgba(177,185,194,0.9);
}

.center {
  /*
  margin: auto;
  width: 100%;
  padding: 10px;
  */
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
}

/* fix image resizing in transition */
.relative {
  position: relative;
}

input[type="radio" i] {
  background-color: initial;
  cursor: default;
  appearance: radio;
  box-sizing: border-box;
  margin: 3px 3px 0px 5px;
  padding: initial;
  border: initial;
}
</style>
